@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
  --transition-base: .3s cubic-bezier(.25, .1, .25, 1);
  --primary-gradient: linear-gradient(90deg, #1eca59 0, #4cbfb0 60%, #1eca59 100%);
}

@layer components {
  .btn {
    padding: 2px 20px;
    display: inline-flex;
    min-height: 50px;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    line-height: 26px;
    font-weight: 500;
    text-decoration: none;
    color: theme("colors.white");
  }
  .btn-outline-primary {
    font-size: 17px;
    line-height: 26px;
    font-weight: 500;
    color: theme("colors.white");
    border: 4px solid rgba(255, 255, 255, 0.3);
    border-radius: 40px;
  }
}
/* Plugins */
/* fade image in after load */
.lazyload,
.lazyloading {
  opacity: 0;
  transition: opacity var(--transition-base);
}

.lazyloaded {
  opacity: 1;
  transition: opacity var(--transition-base);
}

