h1,
.h1 {
  font-size: 56px;
  line-height: 84px;
  font-weight: 700;
}
@media only screen and (max-width: theme("screens.mdMax.max")) {
  h1,
  .h1 {
    font-size: 28px;
    line-height: 135%;
    font-weight: 700;
  }
}

p,
.p {
  font-size: 17px;
  line-height: 26px;
  font-weight: 500;
}
b,
strong {
  font-weight: 700;
}

a {
  font-size: 17px;
  line-height: 26px;
  font-weight: 500;
  text-decoration: underline;
}
a:hover {
  text-decoration: none;
}

input:focus {
  outline: none;
}

textarea:focus {
  outline: none;
}

button {
  padding: 0;
  border: 0;
  cursor: pointer;
}
button:focus {
  outline: none;
}

input[type=submit] {
  border: 0;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

div,
a,
a:active,
a:focus {
  outline: none;
}

html {
  height: 100%;
  min-height: 100%;
  @apply font-sans;
}

body {
  display: flex;
  height: 100%;
  min-height: 100%;
  flex-direction: column;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 500;
  color: theme("colors.white");
  background: theme("colors.secondary");
  -webkit-font-smoothing: antialiased;
  @apply font-sans;
}

/* Blocks */
.main {
  flex: 1 0 auto;
}
.section--hero {
  padding: 132px 0 0;
  background: theme("colors.primary");
}
@media only screen and (max-width: theme("screens.mdMax.max")) {
  .section--hero {
    padding: 104px 0 0;
  }
}

.header {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  padding: 32px 48px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: theme("screens.mdMax.max")) {
  .header {
    padding: 24px;
  }
}
.header .btn {
  padding: 2px 16px;
}
@media only screen and (max-width: theme("screens.mdMax.max")) {
  .header__logo {
    width: 99px;
  }
}

.hero__img {
  margin-top: -345px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: theme("screens.mdMax.max")) {
  .hero__img {
    margin-top: 48px;
  }
}
.hero__img img {
  display: block;
}
.hero__text-wrap {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: theme("screens.mdMax.max")) {
  .hero__text-wrap {
    margin-bottom: 48px;
    flex-direction: column;
    justify-content: flex-start;
    gap: 48px;
  }
}
.hero__text-wrap p {
  font-size: 22px;
  line-height: 33px;
  font-weight: 600;
}
@media only screen and (max-width: theme("screens.mdMax.max")) {
  .hero__text-wrap p {
    font-size: 17px;
    line-height: 26px;
  }
}
@media only screen and (min-width: theme("screens.md")) {
  .hero__text-left {
    max-width: 350px;
  }
}
@media only screen and (min-width: theme("screens.md")) {
  .hero__text-right {
    max-width: 392px;
  }
}

.footer__border {
  border-top: 2px solid rgba(130, 154, 190, 0.1);
}